.container{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    margin: auto;
   gap:30px;
   width: 65%;
}
.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  width: 200px;
    height: 200px;
    background-color: rgba(248, 250, 247, 0.956);
    transition: transform 1s;
}
