.intro {
  display: flex;
  margin-top: 4rem;
  justify-content: space-between;
  align-items: center;
  background-color: #121ab0;
  height: 600px;
  background-image: linear-gradient(to right, rgba(14, 42, 134, 0), rgb(28, 42, 232));
}

.i-left {
  display: flex;
  position: relative;
  flex-direction: column;
}

.i-name {
  display: flex;
  flex-direction: column;
  padding: 70px;
  width: 60%;
}

.i-name>:nth-child(1) {
  color: rgb(239, 233, 233);
  font-weight: bold;
  font-size: 2rem;

}

.i-name>:nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 2.5rem;

}

.i-name>:nth-child(3) {
  color: var(--gray);
  font-size: 14px;
  margin-top: 10px;
}

.i-button {
  width: 7rem;
  height: 2rem;
  margin-top: 5%;
}

#cont {
  color: rgb(239, 233, 233);
}

.me {
  width: 100%;
  border-radius: 10%;
  margin: auto;
  height: 355px;
  align-items: center
}

@media screen and (max-width:1024px) {
  .i-left {
    width: 50%;
  }

  .i-name {
    padding: 100px;
  }

  .i-name>:nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 1rem;

  }

  .i-name>:nth-child(2) {
    font-size: 1.5rem;
  }

  /* .i-right {

    width: 50%;
  } */

  .me {
    width: 80%;
    border-radius: 10px;
    margin: auto;
    display: flex;
    align-items: center;
    height: 200px;
    
  }

}

@media screen and (max-width:600px) {
  .intro {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: auto;
    margin-top: 50px;
    /* margin-left: 100px; */
  }

  .i-left {
    width: 100%;
    padding: 0px;
    margin-right: 3px;
    margin-top: -60px;
  }

  i.name {
    padding: 0px;
  }

  .me {
 height:250px
  }

  .i-right {
  margin-left:100px;
  margin-bottom:10px;
  margin-top: -50px;
  } 
}