.contact {
    display: flex;
    justify-content: space-around;
    margin-top: 120px;
}

.contact-heading {
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    font-family: sans-serif;
}

.dev-left {
    width: 30%;
}

.dev-left>img {
    width: 100%;
}

.email {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.message {
    width: 100%;
    height: 100px;
}

.dev-right {

    width: 40%;
}

.input {
    display: flex;
    justify-content: space-between;
}

.input input {
    padding: 12px;
}

.message {
    margin-top: 40px;
    width: 100%;

}

.message textarea {
    width: 100%;
    height: 150px;
}
#btn_div{
    margin-top: 70px;
}
#btn_div hover{
   cursor: pointer;
}
.btnemail {
    padding: 10px;
    font-size: 25px;
    font-weight: bold;
    font-family: sans-serif;
    width: 40%;
    border-radius: 10px;
    background-color: #ffd447;
    color: white;
}