#cn{
    width: 50%;
    margin: auto;
    
    border-radius: 10px;
    transition: transform 1s;
    margin-top: 50px;
}
#cn:hover{
transform: rotate(2deg);
}
.stats1{

    border: none;
    width: 100%;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    margin: auto;
    
}
#first{
    transition: transform 1s;
}
#first:hover{
    transform: rotate(3deg);
}
.stats2{
    /* border: 1px solid red; */
    margin:auto;
    display:flex;
    width:55%;
    height:300px;
    justify-content:space-between;
    align-items:center;
    
}
#second{
    transition: transform 1s;
}
#second:hover{
    transform: rotate(3deg);
}
@media screen and (max-width:1024px ) {
    #cn{
        margin-top: -30px;
    }
    .stats2{
       
        margin:auto;
        display:grid;
        grid-template-columns: repeat(1,1fr);
        width:50%;
        height:350px;
        justify-content:space-between;
        align-items:center
    }
    #first{
        /* border: 1px solid red; */
        margin: auto;
    }
    #first>img{
        /* border: 1px solid ; */
        margin:auto
    }
    #second{
        margin: auto;
    }
}

@media screen and (max-width:600px ) {
   #cn{
    margin-top: 20px;
   }
    .stats2{
        margin:auto;
        display:grid;
        grid-template-columns: repeat(1,1fr);
        width:50%;
        justify-content:space-between;
        align-items:center;
        height: 150px;
    }
    #first{
        margin: auto;
        width: 50%;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    }
    #first>img{
        border: 1px solid ;
        margin:auto
    }
    #second{
        margin: auto;
        width: 100%;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    }
}