.n-wrapper{
    height:50px;
    display:flex;
    /* justify-content: space-between; */
    border: 1px solid;
    /* font-family: var(--font-family); */
    /* margin-top: 10px; */
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    width:100%;
    /* margin-right: 100px; */
    /* margin-left: -65px; */
    padding: 15px;
   position: fixed;
   z-index: 50;
   /* position: static; */
   background-color:#010230 ;
}
.n-left{
    /* border: 1px solid blue; */
    /* flex:1; */
   align-items: center;
   display: flex;
   gap:2rem;
   width:40%
   /*  */
}

.n-name{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    /* font-family: var(--font-family); */
    /* background-color: rgb(255, 255, 255) ; */
}
.n-name:hover{
cursor: pointer;
}

.n-right{
    /* border: 1px solid red; */
    display: flex;
    /* flex:1; */
    align-items: center;
    justify-content: center;
    font-weight: 400;
   width: 60%;
}

.n-list{
 margin-left: 1rem;
}

.n-list>ul{
    display: flex;
    gap:2rem;
    margin-right: 4rem;
  
}
li{
    color:white;
}
.n-list>ul>li:hover{
  cursor: pointer;
  color:var(--teal)
}

.button{
    margin-left: 10px;
}

/* .button:hover{
   background-color: var(--teal);
} */

@media screen and (max-width:1024px ) { 
    .n-wrapper{
        height:40px;
        display:flex;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        width:97%;
        margin: auto;
        /* margin-left: -30px; */
        padding: 15px;
       /* position: fixed; */
       /* font-size: 15px */
    }
    .n-left{
        width:40%;
       align-items: center;
       display: flex;
       gap:1rem
    }

    .n-right{
        width: 60%;
    }
    .n-name{
        font-size: 0.8rem;
        font-weight: bold;
        /* font-family: var(--font-family); */
    }
    #t{
        font-size: 12px;
        font-weight: bold;
    }
    .n-list{
        font-size: 15px;
        width: 70%;
    }
    .n-list>ul{
        display: flex;
        gap:1rem;
        padding: 15px;
        
    }
    .n-list>ul>li{
        font-size: 12px;
        font-weight: bold;
    }
    .button{
        /* margin-left: 10px; */
        /* width:10%; */
        font-size: 12px;
    }
}

